<template>
  <div class="new-sign">
    <el-form class="c-search-form" :model="reqData" ref="reqData" label-width="122px" :inline="true"
             label-position="right">
      <el-row>
        <el-col :span="8">
          <el-form-item label="商场名称">
            <selectMarket :reqData="reqData"/>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="商户名称">
            <el-select v-model.trim="reqData.merchantName" filterable placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="手机号">
            <el-input clearable v-model.trim="reqData.phone"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :offset="16" :span="8">
          <div class="c-footer-btn">
            <el-button class="btn-search" type="primary" icon="el-icon-search" @click="search">查询</el-button>
          </div>
        </el-col>
      </el-row>
    </el-form>

    <div class="c-table-wrap">
      <el-table
        :data="itemList"
        style="width: 100%"
        height="500"
        align="center"
        stripe>
        <el-table-column
          align="center"
          label="业务编号"
          prop="num"
          min-width="100">
        </el-table-column>
        <el-table-column
          align="center"
          label="所属商场"
          prop="merchantName"
          min-width="120">
        </el-table-column>
        <el-table-column
          align="center"
          prop="merchantName"
          label="签约商户"
          min-width="120">
        </el-table-column>
        <el-table-column
          align="center"
          prop="marketName"
          label="代表人"
          min-width="120">
        </el-table-column>
        <el-table-column
          align="center"
          prop="leaderTel"
          label="手机号"
          min-width="120">
        </el-table-column>
        <el-table-column
          align="center"
          label="状态"
          min-width="110">
          <template slot-scope="scope">
            <span>{{ scope.row.paymentStatus ? scope.row.paymentStatus.desc:'' }}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="latestPayTime"
          label="签约摊位"
          min-width="165">
        </el-table-column>
        <el-table-column
          align="center"
          prop="payAmount"
          label="使用面积">
        </el-table-column>
        <el-table-column
          align="center"
          label="签约租赁周期"
          min-width="210">
          <template slot-scope="scope">
            <!--<span v-if="scope.row.rentBeginTime">{{scope.row.rentBeginTime}}至{{scope.row.rentEndTime}}</span>-->
            <span v-if="scope.row.rentBeginTime">{{scope.row.rentBeginTime}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="计租期"
          min-width="210">
          <template slot-scope="scope">
            <!--<span v-if="scope.row.rentBeginTime">{{scope.row.rentBeginTime}}至{{scope.row.rentEndTime}}</span>-->
            <span v-if="scope.row.rentBeginTime">{{scope.row.rentBeginTime}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="优惠期"
          min-width="210">
          <template slot-scope="scope">
            <!--<span v-if="scope.row.rentBeginTime">{{scope.row.rentBeginTime}}至{{scope.row.rentEndTime}}</span>-->
            <span v-if="scope.row.rentBeginTime">{{scope.row.rentBeginTime}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="createTime"
          label="签约日期"
          min-width="165">
        </el-table-column>
        <el-table-column
          align="center"
          prop="marketName"
          label="楼层经理"
          min-width="120">
        </el-table-column>
        <el-table-column
          align="center"
          label="审批信息"
          min-width="120">
          <template slot-scope="scope">
            <span class="view-info">{{ scope.row.paymentStatus ? scope.row.paymentStatus.desc:'' }}</span>
            <a class="a-view view-info-btn" @click="viewSchedule(scope.row.contractBatchNum)">查看</a>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="createTime"
          label="合同信息"
          min-width="90">
          <template slot-scope="scope">
            <a class="a-view" @click="viewContract(scope.row.contractBatchNum)">查看</a>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="createTime"
          label="账单信息"
          min-width="90">
          <template slot-scope="scope">
            <a class="a-view" @click="viewContract(scope.row.contractBatchNum)">查看</a>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="marketName"
          label="登记人"
          min-width="120">
        </el-table-column>
        <el-table-column
          align="center"
          prop="createTime"
          label="登记时间"
          min-width="165">
        </el-table-column>
        <el-table-column
          align="center"
          width="300"
          fixed="right"
          label="操作">
          <template slot-scope="scope">
            <el-button class="btn-handle" type="primary">审核</el-button>
            <el-button class="btn-handle" type="primary">续签</el-button>
            <el-button class="btn-handle" type="primary">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--分页-->
    <pagenation :pageparams="pageInfo" :itemList="itemList" @handlePage="handlePage"/>

    <!--查看进度-->
    <el-dialog
      title="合同审核进度"
      :visible.sync="scheduleDialog"
      width="30%"
      class="m-dialog el-steps-dialog">
      <el-steps :space="200" :active="curProgressActive" align-center >
        <el-step v-for="(item, index) in progressList"
                 :key="index" :title="item.verifierCharacter"
                 :description="item.verifierName"></el-step>
      </el-steps>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="scheduleDialog = false" class="btn-save">关闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  import Api from '@/api/financial'
  import { pageListMixin } from '@/utils/mixin'
  import selectMarket from '@/components/selectMarket'
export default {
  name:'contractReceipts',
  mixins: [pageListMixin],
  data() {
    return {
      reqData:{
        merchantName:'',
        contractBatchNum:'',
        phone:'',
        currentPage:1
      },
      scheduleDialog:false,
      progressList:[], //审核进度
      options:[],
    }
  },
  computed:{
    curProgressActive() {
      for(let i=this.progressList.length; i>0 ; i--){
        if(this.progressList[i-1].checkStatus && this.progressList[i-1].checkStatus.code === 4){
          return i
        }
      }
      return 0
    },
  },
  mounted() {
    let marketCode = this.$route.params.marketCode || '';
    if(marketCode) this.reqData.marketCode = marketCode;
    this.getList(Api.contractCosts);
  },
  methods: {
    toPage(contractBatchNum) {
      this.$router.push({name:'contractRecCashIn', params:{ contractBatchNum : contractBatchNum}})
    },
    search() {
      this.reqData.currentPage = 1;
      this.getList(Api.contractCosts);
    },
    async viewSchedule(contractBatchNum) {
      let res = await this.$http.get(`${Api.checkProgress}/${contractBatchNum}`);
      if(res){
        this.progressList = res.data
      }
      this.scheduleDialog = true;
    },
    async viewContract(contractBatchNum) {

    }
  },
  components:{
    selectMarket
  }
}
</script>
<style lang="less" scoped>
  .new-sign{
    .view-info{
      display: inline-block;
      width: 56px;
      text-align: left;
    }
    .view-info-btn{
      position: absolute;
      top: 50%;
      right: 12px;
      transform: translateY(-50%);
    }
   .a-view{
     color: #1989FA;
   }
  }
</style>



